import React, { useState, useEffect } from "react";
import cx from "classnames";
import style from "./Search.module.css";
import { Pagination } from "../../Pagination";

export const SearchEList = ({ track, artist, album, playlist, switchView, setCurrentAlbum, setCurrentArtist, setCurrentPlaylist }) => {
  const playlistSrc =
    "data:image/png;base64,";
  const artistSrc =
    "data:image/png;base64,";
  const albumSrc =
    "https://cdn1.iconfinder.com/data/icons/weby-flat-multimedia/64/multimedia-22-512.png";
  const songSrc =
    "https://lh3.googleusercontent.com/proxy/k7o1IUmmfsHpRW5vq-ATiD9KmTxhAfVs2q4w9_UPkf1_ZNnqZCCwOisOhKITE2HcUZ24kN57q8-pcMXD6M0ngbpU0Iq10sBZ-hk1_jYSOt0vDzVi7VTWurxijEHl57MPSpcX7Big40T17A";

  const itemsPerPage = 4;
  const [currentSongs, setCurrentSongs] = useState(1);
  const [currentArtists, setCurrentArtists] = useState(1);
  const [currentAlbums, setCurrentAlbumPage] = useState(1);
  const [currentPlaylists, setCurrentPlaylists] = useState(1);

  const paginateSongs = pageNumber => {
    setCurrentSongs(pageNumber);
  };

  const paginateAlbums = pageNumber => {
    setCurrentAlbumPage(pageNumber);
  };

  const paginateArtists = pageNumber => {
    setCurrentArtists(pageNumber);
  };
  const paginatePlaylists = pageNumber => {
    setCurrentPlaylists(pageNumber);
  };

  const openAlbum = id => {
    setCurrentAlbum(id);
    //console.log(id);
    switchView("AlbumDetail");
  }

  const openArtist = id => {
    setCurrentArtist(id);
    switchView("ADetail")
  }

  const openPlaylist = id => {
    setCurrentPlaylist(id);
    switchView("PlaylistDetail")
  }

  //USE ROUTER
  const indexOfLastTrack = currentSongs * itemsPerPage;
  const indexOfFirstTrack = indexOfLastTrack - itemsPerPage;
  const displayedTracks = track.slice(indexOfFirstTrack, indexOfLastTrack);

  const indexOfLastArtist = currentArtists * itemsPerPage;
  const indexOfFirstArtist = indexOfLastArtist - itemsPerPage;
  const displayedArtists = artist.slice(indexOfFirstArtist, indexOfLastArtist);

  const indexOfLastAlbum = currentAlbums * itemsPerPage;
  const indexOfFirstAlbum = indexOfLastAlbum - itemsPerPage;
  const displayedAlbums = album.slice(indexOfFirstAlbum, indexOfLastAlbum);

  const indexOfLastPlaylist = currentPlaylists * itemsPerPage;
  const indexOfFirstPlaylist = indexOfLastPlaylist - itemsPerPage;
  const displayedPlaylists = playlist.slice(
    indexOfFirstPlaylist,
    indexOfLastPlaylist
  );

  return (
    <div className={style.container}>
      {track.length > 0 ? (
        <div className={cx(style.child, style.container)} >
          <div className={style.flex_parent}>
            <h1>Songs</h1>
            <p>
              {
                <Pagination
                  paginate={paginateSongs}
                  total={track.length}
                  currentPage={currentSongs}
                />
              }
            </p>
            <p>{track.length}</p>
          </div>

          {displayedTracks.map(unit => (
            <div className={cx(style.card, style.child)} key={unit.id} >
              <img
                src={
                  unit.album.images.length ? unit.album.images[0].url : songSrc
                }
                style={{ verticalAlign: "middle" }}
                onClick={() => openAlbum(unit.album.id)}
              />
              <p style={{ fontWeight: "bold" }}>{unit.name}</p>

              <p style={{ verticalAlign: "bottom" }}>
                by: {unit.artists[0].name}
              </p>
            </div>
          ))}
        </div>
      ) : null}

      {album.length > 0 ? (
        <div className={cx(style.container, style.child)}>
          <div className={style.flex_parent}>
            <h1>Albums</h1>
            <p>
              {
                <Pagination
                  paginate={paginateAlbums}
                  total={album.length}
                  currentPage={currentAlbums}
                />
              }
            </p>
          </div>

          {displayedAlbums.map(unit => (
            <div className={cx(style.card, style.child)} key={unit.id} onClick={()=> openAlbum(unit.id)}>
              <img
                src={unit.images.length ? unit.images[0].url : albumSrc}
                style={{ verticalAlign: "middle" }}
              />
              <p style={{ fontWeight: "bold" }}>{unit.name}</p>
              <p style={{ verticalAlign: "bottom" }}>{unit.artists[0].name} </p>
            </div>
          ))}
        </div>
      ) : null}

      {artist.length > 0 ? (
        <div className={cx(style.container, style.child)}>
          <div className={style.flex_parent}>
            <h1>Artist</h1>
            <p>
              <Pagination
                paginate={paginateArtists}
                total={artist.length}
                currentPage={currentArtists}
              />
            </p>
          </div>

          {displayedArtists.map(unit => (
            <div className={cx(style.card_artist, style.child)} key={unit.id} onClick={() => openArtist(unit.id)}>
              <img
                src={unit.images.length ? unit.images[0].url : artistSrc}
                style={{ verticalAlign: "middle" }}
              />
              <p style={{ fontWeight: "bold" }}>{unit.name}</p>
            </div>
          ))}
        </div>
      ) : null}

      {playlist.length > 0 ? (
        <div className={cx(style.container, style.child)}>
          <div className={style.flex_parent}>
            <h1>Playlist</h1>
            <p>
              <Pagination
                paginate={paginatePlaylists}
                total={playlist.length}
                currentPage={currentPlaylists}
              />
            </p>
          </div>

          {displayedPlaylists.map(unit => (
            <div className={cx(style.card, style.child)} key={unit.id} onClick={() => openPlaylist(unit.id)}>
              <img
                src={unit.images.length ? unit.images[0].url : playlistSrc}
                style={{ verticalAlign: "middle" }}
              />
              <p style={{ fontWeight: "bold" }}>{unit.name}</p>

              <p style={{ verticalAlign: "bottom" }}>
                Created by: {unit.owner.display_name}
              </p>
            </div>
          ))}
        </div>
      ) : null}
    </div>
  );
};
